<template>
  <div class="home">
  <br>    
        <b-overlay :show="show_load" height="100%" no-wrap></b-overlay>


    <div class="container">
      <div class="row">
        <div class="col-md-12">
           <b-breadcrumb :items="ruta" style=" background-color: #f9f9fc;  font-family: 'Quicksand', sans-serif;" ></b-breadcrumb>
        </div>
      </div>
       <div class="row">
        <div class="col-md-12">
          
           <div class="nav">
             
             <ul class="nav_item">
               <li style="float:left;"><span style="float:left;padding:5px 15px;" @click="retroceder"> <b-icon class="h4" icon="arrow-left"></b-icon></span></li>
                 <li><b-icon class="h4" @click="actualizar" icon="arrow-repeat"></b-icon></li>
               <li> 
                  <b-icon class="h3" ref="button_new_folder"  id="popover-reactive-1"  icon="folder-plus" ></b-icon>
                  
                  <b-popover
                    target="popover-reactive-1"
                    triggers="click"
                    :show.sync="popoverShow1"
                    placement="auto"
                    container="my-container"
                    ref="popover"
                  >
                    <template #title>
                      Crear carpeta
                    </template>
                    <div>
                      <b-input v-model="nueva_carpeta" placeholder="nombre carpeta"></b-input>
                      <b-button variant="primary" @click="crear_carpeta" class="mt-2">Crear</b-button>
                    </div>
                  </b-popover>
     
               </li>
                <li> <b-icon v-b-modal.modal-1 class="h3"  icon="file-plus"></b-icon></li>
                <div>
                  
                    <b-modal id="modal-1" ref="modal-1" title="Subir Archivo" hide-footer>
                  
                    <div class="d-block text-center">
                       <b-form-file id="file-small" class="p-1" size="sm" @change="onImageChanged" name="documento" ref="fileupload" ></b-form-file>
                    </div>
                    <b-button variant="primary" @click="upload" class="mt-3">subir</b-button>
                  </b-modal>

                  
                </div>
             </ul>
              
           </div>
        </div>
      </div>
      <div class="row mt-2">
        <div class="col-md-4"></div>
        <div class="col-md-4">
          <b-toast :variant="variante_toast" id="toast" style="z-index:1000;" title="Mensaje" static no-auto-hide>
            {{mensage_toast}}
          </b-toast>
        </div>
        <div class="col-md-4"></div>
      </div>
      <div class="row">
        <div class="col-md-12">
          <span class="float-right m-2">Carpetas <b-badge variant="primary" >{{numero_carpetas}}</b-badge></span>
          <span class="float-right m-2">Archivos <b-badge variant="primary" >{{numero_archivos}}</b-badge></span>
        </div>
      </div>

     
      <div class="row w-100">
        <div class="col-md-12">
    
 
          <ul class="ficheros">
            <li v-for="contenido in contenidos" v-bind:key="contenido.name" >
               
              
               <b-icon :id="contenido.name" class="option"  icon="three-dots-vertical"></b-icon>
                
                <b-popover  :target="contenido.name" title="Acciones">
                  <b-button variant="danger" @click="eliminar(contenido.name,contenido.directory)">Eliminar</b-button>
                </b-popover>
          
              <div v-if="contenido.directory">
                <div @click="mover(contenido.name)">
                  <img src="../assets/folder.png" width="40%" alt=""><br>
                   {{contenido.name}}
                </div>
                
              </div>
               <a :href="`http://${adress_server}:${port_server}/uploads/`+get_rutas()+'/'+contenido.name">
              <div v-if="!(contenido.directory)">
                <span style="float:left; font-size:12px; color:black; font-weight: bold;">{{contenido.size}}</span>
                <br>
                <img src="../assets/file.png" width="40%" alt=""><br>
               {{contenido.name}}
              </div>
            </a>
            </li>
          </ul>
          
        </div>
   
      </div>
      <br><br><br>
    </div>  
    <br>
    <span style="font-weight:bold;"><b-icon class="h3" icon="github"></b-icon> DarwinMMC</span>

  </div>
</template>

<script>
// @ is an alias to /src
import breadcrumb from '@/components/breadcrumb.vue'
import axios from 'axios'



export default {
  name: 'Home',
  data(){
    return {
      adress_server:"",
      port_server:"",
        ruta: [
          {
          }
        ],
        contenidos:[
          {
            name:'/',
            directory:'',
            size:''
          }
        ],
        popoverShow1:false,
        popoverShow2:false,
        nueva_carpeta:'',
        toast:false,
        mensage_toast:'',
        variante_toast:'',
        show_load:false,
        file:null,
        numero_carpetas:0,
        numero_archivos:0,

    }
  },
  components: {
    breadcrumb
  },
  created(){

    this.adress_server = address
    this.port_server = port
  
    this.consultar_directorios(this.get_rutas())
    
   
  },
  methods:{
    actualizar(){
      this.consultar_directorios(this.get_rutas())
    },  
    get_rutas(){
       let ruta_completa = ""
      for (let index = 1; index < this.ruta.length; index++) {
        ruta_completa +="/"+this.ruta[index].text
      }
      
      return ruta_completa
    },
    retroceder(){
      if(this.ruta.length>1){
        this.ruta.pop()
        this.consultar_directorios(this.get_rutas())
      }
     
    },
    mover(ruta){
      this.ruta.push({
        text: ruta,
        active:true
      })
      this.consultar_directorios(this.get_rutas())
    },
     showModal() {
        this.$refs['modal-1'].show()
      },
      hideModal() {
        this.$refs['modal-1'].hide()
      },
   
  	onImageChanged: function(event) {
      this.file = event.target.files[0];
    },
    upload: function() {
      if((this.file!=null) &&(!!this.file)){
         this.show_load = true
         var formdata = new FormData();
         formdata.append('documento',this.file);
      
         axios.post(`http://${this.adress_server}:${this.port_server}/files?route=`+this.get_rutas(),formdata,{headers:{"Content-Type": "multipart/form-data"}})
         .then(resultado=>{
          this.consultar_directorios(this.get_rutas())
          this.show_load = false
         })
         .catch(error=>{
            this.mensage_toast = "error al subir fichero: "+error
            this.variante_toast = "danger"
            this.$bvToast.show('toast')
            this.show_load = false
         })
          this.hideModal()
         }else{
           this.hideModal()
            this.mensage_toast = "no hay nada para subir"
            this.variante_toast = "warning"
            this.$bvToast.show('toast')
         }
          
  },
    eliminar(name,directory){
       this.show_load = true
        axios.delete(`http://${this.adress_server}:${this.port_server}/files`,
        {
          params:
          {
            route:"/"+name,
            directory:directory
          }
        })
        .then(resultado=>{
       
          this.consultar_directorios(this.get_rutas())
           this.show_load = false
        })
        .catch(error=>{
          console.log(error)
           this.show_load = false
        })
    },
    crear_carpeta(){
      if(this.nueva_carpeta!=""){
        let carpeta_repetida = false;
        for (let index = 0; index < this.contenidos.length; index++) {
          if(this.contenidos[index].name == this.nueva_carpeta){
            carpeta_repetida = true
            break;
          }
        }
        if(!carpeta_repetida){
            axios.post(`http://${this.adress_server}:${this.port_server}/files/new_folder`,{route:this.get_rutas()+"/"+this.nueva_carpeta})
            .then(resultado=>{
              this.popoverShow1 = false
              this.nueva_carpeta = ""
              this.consultar_directorios(this.get_rutas())
            })
            .catch(error=>{
              console.log(error)
            })
        }else{
          this.popoverShow1 = false
      
          this.mensage_toast = "la carpeta ya existe"
          this.variante_toast = "warning"
          this.$bvToast.show('toast')
          
        }
       
      }
    },
    consultar_directorios: function(ruta){

     this.show_load = true
     this.numero_carpetas = 0
     this.numero_archivos = 0
      axios.get(`http://${this.adress_server}:${this.port_server}/files`,{
        params:
        {
          route:ruta
        }
      })
      .then(resultado=>{
        this.contenidos = resultado.data.files

        for (let index = 0; index < this.contenidos.length; index++) {
            if(this.contenidos[index].directory){
              this.numero_carpetas++
            }
        }
        this.numero_archivos = this.contenidos.length - this.numero_carpetas
  
           this.show_load = false
      })
      .catch(error=>{
        console.log(error)
           this.show_load = false
      })
    }
  },

}
</script>

